import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, FormGroup, Row } from 'reactstrap';
import {
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputTooltipProps,
  InputType,
  isEmpty,
  onlyNumber,
  WindowComponent,
  withTooltip,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { CLASS_NAME, GrupoPlanoConta } from '../../../../models/dtos/ger/grupoPlanoConta';
import { DE_PARA, TIPO_PLANO_CONTA_REFERENCIAL_ENUM_VALUES } from '../../../../models/enumerated/mcg/tipoPlanoContaReferencialEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import PlanoContaMCGReferencialInstituicaoIS from '../../../components/mcg/planoContaMCGReferencialInstituicao/PlanoContaMCGReferencialInstituicaoIS';

interface GrupoPlanoContaEditViewProps extends EditViewProps<GrupoPlanoConta> {}

const GrupoPlanoContaEditView: FC<GrupoPlanoContaEditViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<GrupoPlanoConta>({ defaultValues: selectedItem, mode: 'onBlur' });

  const [isEnableDigitoAtivo, setEnableDigitoAtivo] = useState(false);
  const [isEnableDigitoPassivo, setEnableDigitoPassivo] = useState(false);

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  useEffect(
    () => {
      if (isEmpty(formProps.getValues().id)) {
        formProps.reset({
          digitoAtivo: '1',
          digitoDespesa: '4',
          digitoPassivo: '2',
          digitoReceita: '3',
          mascara: '########',
          tipoPlanoContaReferencial: DE_PARA.value,
        });
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = () => {
    const erros = [];

    if (verifyMaskLength() > 25) {
      erros.push('Máscara não deve exceder 25 caracteres.');
    }

    return erros;
  };

  function verifyMaskLength(): number {
    let count = 0;
    for (let i = 0; i < formProps.getValues().mascara.length; i++) {
      if (formProps.getValues().mascara.charAt(i) === '#') {
        count++;
      }
    }
    return count;
  }

  function ajustarDigitoConta(value): void {
    if (value === 'P') {
      formProps.setValue('digitoAtivo', '1');
      formProps.setValue('digitoPassivo', '2');

      setEnableDigitoAtivo(true);
      setEnableDigitoPassivo(true);
    } else {
      setEnableDigitoAtivo(false);
      setEnableDigitoPassivo(false);
    }
  }

  function adjustMaskChars(value): void {
    formProps.setValue('mascara', null);
    let input = '';
    let r = '';

    for (let i = 0; i < value.length; i++) {
      if (value.charAt(i) !== '-' && value.charAt(i) !== '.') {
        r += '#';
      } else {
        r += value.charAt(i);
      }
    }
    input += r;

    formProps.setValue('mascara', input);
  }

  const defaultWidth = 105;

  return (
    <WindowComponent
      title="Manutenção de Grupo de Plano de Conta"
      useGlobalParameter={true}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
      globalParameter={globalReducer.globalParameter}
      wikiId={1346}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>Básico</CardTitle>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 8 } }}
                      id="codigo"
                      isPrependLabel={true}
                      label="Código"
                      labelWidth={defaultWidth}
                      tooltipProps={{ message: 'Preenchimento Opcional - Codificação  Automática' }}
                      type={InputType.NUMBER}
                      placeholder="Informe o código"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { required: true, maxLength: 120 } }}
                      id="descricao"
                      isPrependLabel={true}
                      label="Descrição"
                      labelWidth={defaultWidth}
                      placeholder="Informe a Descrição"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { required: true } }}
                      id="mascara"
                      isPrependLabel={true}
                      label="Máscara"
                      labelWidth={defaultWidth}
                      onChange={adjustMaskChars}
                      placeholder="Informe a máscara do Plano de Contas"
                    />
                  </Col>
                </Row>
                <FormGroup tag={Row}>
                  <Col>
                    <CustomInput
                      id="idutilizarContaSinteticaCompleta"
                      label="Utilizar conta sintética completa"
                      name="utilizarContaSinteticaCompleta"
                      innerRef={formProps.register}
                      type="switch"
                    />
                  </Col>
                  {withTooltip(
                    propToolTip(
                      'Utilizar conta sintética completa. Ex: Conta Ativo - 1.0.0.0000. Uma vez configurada essa opção o sistema' +
                        ' não irá respeitar a hierarquia sequencial de Contas Contábeis.'
                    ),
                    'utilizarContaSinteticaCompleta'
                  )}
                </FormGroup>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>SPED</CardTitle>
                <Row>
                  <Col>
                    <PlanoContaMCGReferencialInstituicaoIS
                      formProps={formProps}
                      id="planoContaReferencialInstituicao"
                      label="Instituição"
                      placeholder="Informe a Instituição"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={formProps}
                      id="tipoPlanoContaReferencial"
                      inputDropDownProps={{ optionsProvider: TIPO_PLANO_CONTA_REFERENCIAL_ENUM_VALUES }}
                      isPrependLabel={true}
                      label="Plano de Contas Referencial"
                      onChange={ajustarDigitoConta}
                      placeholder="Informe o Plano de Contas Referencial"
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
              <CardBody>
                <CardTitle>Conta</CardTitle>
                <Row>
                  <Col>
                    <InputGroupInline
                      disabled={isEnableDigitoAtivo}
                      formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 1) } }}
                      id="digitoAtivo"
                      isPrependLabel={true}
                      label="Ativo"
                      labelWidth={defaultWidth}
                      placeholder="Informe o dígito do Ativo"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      disabled={isEnableDigitoPassivo}
                      formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 1) } }}
                      id="digitoPassivo"
                      isPrependLabel={true}
                      label="Passivo"
                      labelWidth={defaultWidth}
                      placeholder="Informe o dígito do Passivo"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { ...onlyNumber(0, 3) } }}
                      id="digitoPatrimonioLiquido"
                      isPrependLabel={true}
                      label="Grupo Patrimônio Líquido"
                      placeholder="Informe o dígito do Grupo Patrimônio Líquido"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 1) } }}
                      id="digitoReceita"
                      isPrependLabel={true}
                      label="Receita"
                      labelWidth={defaultWidth}
                      placeholder="Informe o dígito da Receita"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 1) } }}
                      id="digitoDespesa"
                      isPrependLabel={true}
                      label="Despesa"
                      labelWidth={defaultWidth}
                      placeholder="Informe o dígito da Despesa"
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoPlanoContaEditView;
