import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mcg/planoContaReferencialInstituicao';

const PlanoContaMCGReferencialInstituicaoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'instituicao',
            text: 'Instituição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'PlanoContaMCGReferencialInstituicaoSearchView',
      }}
      label={props.label ? props.label : 'Plano de Contas Referencial'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Plano de Contas Referencial'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default PlanoContaMCGReferencialInstituicaoIS;
